<template>
  <div>
    <template>
      <v-container>
        <h1>Registrar Clientes</h1>
        <v-form ref="form" @submit.prevent="agregarCliente()">
          <v-text-field
            v-model="atributos.identificacion"
            :counter="10"
            :rules="identificacionRules"
            label="Identificacion"
            required
          ></v-text-field>

          <v-text-field
            v-model="atributos.nombre"
            :counter="30"
            label="Nombre"
            required
          ></v-text-field>

          <v-text-field
            v-model="atributos.apellidos"
            :counter="30"
            label="Apellidos"
          ></v-text-field>        

          <v-text-field
            v-model="atributos.direccion"
            :counter="50"
            label="Direccion"
          ></v-text-field> 

          <v-text-field
            v-model="atributos.email"
            :counter="40"
            :rules="emailRules"
            label="Email"
            required
          ></v-text-field>

          <v-text-field
            v-model="atributos.telefono"
            :counter="15"
            label="Teléfono"
          ></v-text-field>

          <v-btn
            color="success"
            class="mr-4"
            type="submit"
            @click="validate">
            Enviar registro
          </v-btn>

          <v-btn
            color="error"
            class="mr-4"
            @click = "reset"            
          >
            Limpiar
          </v-btn>

        </v-form>
    </v-container>
    </template>
  </div>
</template>

<script>
  export default {
    data: () => ({
      valid: true,
      identificacion: '',
      identificacionRules: [
        v => !!v || 'Se requiere identificación',
        v => (v && v.length <= 10) || 'Debe contener máximo 10 caracteres',
      ],
      email: '',
      emailRules: [
        v => !!v || 'Email es requerido',
        v => /.+@.+\..+/.test(v) || 'El Email debe ser valido',
      ],
      select: null,
      Atributos:[],

      atributos:{
        identificacion:"",
        nombre:"",
        apellidos:"",
        direccion:"",
        email:"",
        telefono:"",        
      },      

    }),
    methods:{
      validate() {
        this.$refs.form.validate()
      },
      reset () {
        Atributos:[],
        this.$refs.form.reset()
                        
      },

      resetValidation() {
        this.$refs.form.resetValidation()
      },


      agregarCliente(){
        console.log(this.atributos);
        if(!this.atributos.identificacion){
            this.$swal('Error!',
                        'Falta Identificación!',
	                      'error');
        } else if(!this.atributos.nombre){
            this.$swal('Error!',
                        'Falta Nombre!',
	                      'error');
        } else if(!this.atributos.apellidos){
            this.$swal('Error!',
                        'Falta Apellidos!',
	                      'error');
        }else if(!this.atributos.direccion){
            this.$swal('Error!',
                        'Falta Dirección!',
	                      'error');
        }else if(!this.atributos.email){
            this.$swal('Error!',
                        'Falta Email!',
	                      'error');
        }
        else if(!this.atributos.telefono){
            this.$swal('Error!',
                        'Falta Teléfono!',
	                      'error');
        }        
        else{
          this.axios
          .post("/nuevo-registro", this.atributos)
          .then((res)=>{ 
                this.Atributos.push(res.data);

                this.$swal('success!',
                        'Registro adicionado satisfactoriamente!',
	                      'success');

                this.atributos.identificacion='';
                this.atributos.nombre='';
                this.atributos.apellidos='';
                this.atributos.direccion='';
                this.atributos.email='';
                this.atributos.telefono='';                
                
                //alert("Registro adicionado satisfactoriamente");                
              })
              .catch((e)=>{
              console.log(e.response);
              alert("Error al adicionar registro"); 
              })

        }

        
        }

      }    
  }
</script>